import { connect } from 'react-redux'
import Payments from '../components/Payments'
import { IState } from '../reducers'

const mapStateToProps = (state: IState) => ({
	card: state.user.card,
})
const mapDispatchToProps = {}

export default connect(
	mapStateToProps,
	mapDispatchToProps,
)(Payments)



