import React from 'react'
import { INotification, NotificationSeverity } from '../../reducers/notifications'
import styles from './styles.sass'
import cn from 'classnames'
import { INotifications } from '../../actions/notifications'

export interface INotificationsProps {
	notifications: INotifications
}

export interface INotificationsActionProps {
	dismissNotification: (notification: INotification) => void
}

export default class Notifications extends React.Component<
	INotificationsProps & INotificationsActionProps
> {
	render() {
		const { notifications, dismissNotification } = this.props

		return (
			<div className={styles.wrapper}>
				{Object.values(notifications).map((item) => (
					<div key={item.id} className={cn(
						styles.item,
						item.severity === NotificationSeverity.Warning && styles.viewWarning,
						item.severity === NotificationSeverity.Danger && styles.viewDanger,
					)}>
						<div className={styles.content}>
							{item.title}
						</div>
						<button
							role="button"
							onClick={() => dismissNotification(item)}
							className={styles.dismiss}
							aria-label="Dismiss notification"
						>
							<i className="fas fa-times"></i>
						</button>
					</div>
				))}
			</div>
		)
	}
}



