import React from 'react'
import styles from './styles.sass'
import Popup from '../Popup'
import { IPaymentCard } from '../../models/payment'

export interface IPaymentsOwnProps {
	isActive: boolean
	onClose: () => void
}

export interface IPaymentsProps {
	card: IPaymentCard
}

export interface IPaymentsActionProps {}

export default class Payments extends React.Component<
	IPaymentsOwnProps & IPaymentsProps & IPaymentsActionProps
> {

	render() {
		const { card, isActive, onClose } = this.props

		return (
			<Popup isActive={isActive} onClose={onClose}>
				<div className={styles.wrapper}>
					{card && (
						<div>
							{card.issuer} {card.number}
							<button
								role="button"
								onClick={this.removeCard}
								aria-label="Remove card"
							>
								<i className="fas fa-trash"></i>
							</button>
						</div>
					)}
				</div>
			</Popup>
		)
	}

	private removeCard = () => {
		alert('@TODO: Not implemented')
	}
}



